import { newArrayProto } from "./array";

export function observe(data) {
  // Âä´ÊåÅÊï∞ÊçÆ
  console.log(data);

  // Âè™ÂØπÂØπË±°ËøõË°åÂä´ÊåÅ
  if (typeof data !== 'object' || data === null) return

  // ËØ¥ÊòéËøô‰∏™ÂØπË±°Ë¢´‰ª£ÁêÜËøá‰∫Ü
  if (data.__ob__ instanceof Observer) {
    return data.__ob__;
  } 
  // Â¶ÇÊûú‰∏Ä‰∏™ÂØπË±°Ë¢´Âä´ÊåÅËøá‰∫ÜÔºå ÈÇ£Â∞±‰∏çÈúÄË¶ÅÂä´ÊåÅ‰∫ÜÔºàË¶ÅÂà§Êñ≠‰∏Ä‰∏™ÂØπË±°ÊòØÂê¶Ë¢´Âä´ÊåÅËøáÔºåÂèØ‰ª•Â¢ûÊ∑ª‰∏Ä‰∏™ÂÆû‰æãÔºåÁî®ÂÆû‰æãÊù•Âà§Êñ≠ÊòØÂê¶Ë¢´Âä´ÊåÅËøáÔºâ
  return new Observer(data)
  
}
class Observer {
  constructor(data) {
    // object.definerProperty Âè™ËÉΩÂä´ÊåÅÂ∑≤ÁªèÂ≠òÂú®ÁöÑÊï∞ÊçÆÔºåÊñ∞Â¢ûÁöÑÂíåÂà†Èô§ÁöÑÂπ∂‰∏çËÉΩÊÑüÁü•„ÄÇ
    // vue2 ÈáåÈù¢‰ºö‰∏∫Ê≠§ÂçïÁã¨ËÆæÁΩÆ$set $delete
    // data.__ob__ = this // ÁªôÊï∞ÊçÆÂä†‰∫Ü‰∏Ä‰∏™Ê†áËØÜÔºåÂ¶ÇÊûúÊï∞ÊçÆ‰∏äÊúâ__ob__ÂàôËØ¥ÊòéËøô‰∏™Â±ûÊÄßË¢´ËßÇÊµãËøá
    // (‰ΩÜÊòØÁõ¥Êé•ÊîæÂú®ËøôÈáå‰ºöÊ†àÂÜÖÂ≠òÊ∫¢Âá∫ÔºåÂõ†‰∏∫‰∏ãÈù¢ËøòÊúâwalkÂáΩÊï∞Âæ™ÁéØÔºå ‰∏ãÈù¢Êúâ‰∏™observeÈÄíÂΩí)
    // ÊâÄ‰ª•Â∞Ü__ob__ ËΩ¨Êç¢Êàê‰∏çÂèØÊûö‰∏æ
    Object.defineProperty(data, '__ob__', {
      value: this,
      enumerable: false // Â∞Ü__ob__ËΩ¨Êç¢‰∏∫‰∏çÂèØÊûö‰∏æÔºàÂæ™ÁéØÁöÑÊó∂ÂÄôÊó†Ê≥ïËé∑ÂèñÔºâ
    })

    // ‰ΩÜÊòØÈíàÂØπdata‰∏≠ÁöÑÊï∞ÁªÑÊï∞ÊçÆÊØè‰∏ÄÈ°πÈÉΩÊ∑ªÂä†get(), set() ÊÄßËÉΩÂ§™Â∑Æ‰∫ÜÔºå ‰∏î‰∏çËÉΩÁõëÂê¨Âà∞Êï∞ÁªÑÁöÑpush, pop Á≠âÊñπÊ≥ï
    if (Array.isArray(data)) {
      // ÊâÄ‰ª•Âú®ËøôÈáåÊàë‰ª¨ÈáçÂÜôÊï∞ÁªÑ‰∏≠ÁöÑÊñπÊ≥ï ‰∏É‰∏™ÂèòÂºÇÊñπÊ≥ïÊòØÂèØ‰ª•‰øÆÊîπÊï∞ÁªÑÊú¨Ë∫´ÁöÑ
      data.__proto__ = newArrayProto
      this.observeArray(data) // Â¶ÇÊûúÊï∞ÁªÑ‰∏≠ÊîæÁöÑÊòØÂØπË±°ÁöÑÊó∂ÂÄô
    } else {
      this.walk(data)
    }
  }

  walk(data) {
    // Âæ™ÁéØÂØπË±° ÂØπÂ±ûÊÄß‰æùÊ¨°Âä´ÊåÅ
    // ÈáçÂÜôÂÆö‰πâÂ±ûÊÄß ÊÄßËÉΩÂ∑Æ
    Object.keys(data).forEach(key => defineReactive(data, key, data[key]))
  }

  observeArray(data) { // ËßÇÊµãÊï∞ÁªÑ‰∏≠ÁöÑÂØπË±°Êï∞ÊçÆ
    data.forEach(item => observe(item))
  }
}

export function defineReactive(target, key, value) { // Èó≠ÂåÖ Â±ûÊÄßÂä´ÊåÅ
  observe(value)  // ÈÄíÂΩíÔºå ÊØîÂ¶Çdata‰∏≠ÁöÑÂ±ûÊÄßËøòÊòØ‰∏Ä‰∏™ÂØπË±°ÁöÑÂú∫ÊôØ
  Object.defineProperty(target, key, {
    get() {
      // ÂèñÂÄºÁöÑÊó∂ÂÄô‰ºöÊâßË°åget
      return value
    },
    set(newVal) {
      // ‰øÆÊîπÁöÑÊó∂ÂÄô‰ºöÊâßË°å
      if (newVal === value) return
      value = newVal
    }
  })
}
