// Êàë‰ª¨Â∏åÊúõÈáçÂÜôÊï∞ÁªÑ‰∏≠ÁöÑÈÉ®ÂàÜÊñπÊ≥ï

let oldArrayProto = Array.prototype // Ëé∑ÂèñÊï∞ÁªÑÁöÑÂéüÂûã
 
// ÈÄöËøá newArrayProto.__proto__ = oldArrayProto
export let newArrayProto = Object.create(oldArrayProto) // ÁîüÊàêÊñ∞ÁöÑ(‰∏çÊòØÊã∑Ë¥ùÔºå ÊòØÂ§çÂà∂)

let methods = [
  'push',
  'pop',
  'shift',
  'unshift',
  'reverse',
  'sort',
  'splice'
]

methods.forEach(method => {
  newArrayProto[method] = function(...args) { // ËøôÈáåÈáçÂÜô‰∫ÜÊï∞ÁªÑÁöÑÊñπÊ≥ï
    const result = oldArrayProto[method].call(this, ...args) // ÂÜÖÈÉ®Ë∞ÉÁî®ÂéüÊù•ÁöÑÊñπÊ≥ïÔºå ÂáΩÊï∞ÁöÑÂä´ÊåÅÔºå ÂàáÁâáÁºñÁ®ã
    console.log('method', method);
    // ‰ΩÜÊòØÂØπÊï∞ÁªÑËøõË°åËøΩÂä†È¢ùÂ§ñÁöÑÂØπË±°Êï∞ÊçÆÈúÄË¶ÅÂÜçÊ¨°ËøõË°åÂä´ÊåÅ
    let inserted;
    let ob = this.__ob__;
    switch (method) {
      // ÈúÄË¶ÅÂØπÊñ∞Â¢ûÁöÑÊï∞ÊçÆÂÜçÊ¨°ËøõË°åÂä´ÊåÅ
      case 'push':
      case 'unshift':
        inserted = args
        break;
      case 'splice': 
        // arr.splice(0, 1, {a: 1}, {b: 2})
        // args ÊòØ‰∏™Êï∞ÁªÑÔºåÂè™Êà™ÂèñÊñ∞Â¢ûÁöÑÊï∞ÊçÆÂç≥ÂèØ
        inserted = args.slice(2)
        break
      default:
        break;
    }
    console.log(inserted);
    // ÂØπÊñ∞Â¢ûÁöÑÂÜÖÂÆπÂÜçÊ¨°ËøõË°åËßÇÊµã inserted ÊòØ‰∏™Êï∞ÁªÑÂì¶
    // ÊòØ‰∏çÊòØÊÉ≥Ë∞ÉÁî®observeArray(data) Â∞±ÂèØ‰ª•‰∫ÜÔºå ‰ΩÜÊòØËÆøÈóÆ‰∏çÂà∞üê∂„ÄÇ„ÄÇ„ÄÇ
    if (inserted) {
      // ËøôÈáåÁöÑthis ‰∏çÂ∞±ÊòØÂ§ñÈÉ®ÁöÑdataÂêóÔºåÂõ†‰∏∫Â§ñÈÉ®ÊòØdataË∞ÉÁî®ÁöÑÂïäÔºå
      // ÊâÄ‰ª•Âè™ËÉΩÂú®Â§ñÈÉ®ÁöÑclass Observer ‰∏≠ÁªôdataÂä†‰∏ä‰∏Ä‰∏™Â±ûÊÄßËøôÈáåÂ∞±ËÉΩËÆøÈóÆÂà∞observeArray(Ôºâ‰∫Ü
      // console.log(this);
      ob.observeArray(inserted)
    }
    return result
  }
})
